@import 'Colors.css';

.ProductContainer {
    padding: 0 40px 40px 40px;
}

.ProductContainer > div {
    padding-top: 25px;
    justify-content: center;
    display: flex;
}

.PageTitle {
    text-transform: capitalize;
}


.ThumbnailsContainer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.ThumbnailImageContainer {
    width: 75px;
    height: 75px;
    margin: 10px;
    border: 1px solid lightgrey;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.Selected {
    border: 3px solid red;
}

.SelectedProductImage {
    text-align: center;
    cursor: pointer;
}

.SelectedProductImage > img {
    width: 80%;
}

.QtyAdjustmentContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
}



.ProductDescription {
    text-align: left;
    padding-top: 0;
    margin-top: 0;
}

.ProductImagesContainer,
.ProductInfoContainer {
    max-width: 600px;
    flex-basis: 50%;
}

.ProductInfoContainer {
    display: flex;
    flex-direction: column;
}

.AddToCartButton {
    font-size: 1.5rem;
    margin: 20px 0;
    background-color: var(--orange);
}


@media (max-width: 1000px) {
    .ProductContainer > div {
        flex-direction: column;
        align-items: center;
        padding-top: 0;
    }

    .ProductImagesContainer,
    .ProductInfoContainer {
        /* max-width: 600px; */
        flex-basis: 100%;
    }

    .ProductDescription {
        margin-top: 25px;
    }
}
