.FullScreenImageContainer {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(1, 1, 1, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.FullScreenImage {
    height: 100%;
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.FullScreenImage > img {
    height: 100%;
    width: 100%;
    height: auto;
    width: auto;
    max-height: 100%;
    max-width: 100%;
}
