@import 'Colors.css';

.CartPageContainer {
    padding: 0 40px;
}

.CartItem:nth-child(1) {
    border-top: 1px solid var(--grey);
}

.CartItem {
    border-bottom: 1px solid var(--grey);
    display: flex;
    align-items: center;
    padding: 20px 5px;
}

.CartItemImage {
    flex-basis: 100px;
    min-width: 75px;
}

.CartItemName {
    flex-grow: 1;
    text-align: left;
    padding: 0 20px;
    text-transform: capitalize;
}

.CartItemPrice {
    flex-basis: 100px;
}

.CheckoutButton {
    margin: 50px auto;
    display: block;
    font-size: 1.5rem;
    padding: 7px 25px;
    background-color: var(--orange);
}

@media (max-width: 450px) {
    .CartPageContainer {
        padding: 0 15px;
    }
}